@import '../../../assets/mixins/mixins.scss';


.QuestionnaireCardHome {
    width: fit-content;
    height: 130px;
    width: 250px;
    display: flex;
    flex-direction: column;
    margin: auto;

    & .QuestionnaireTitle {
        
    }

    &__Options {
        margin-bottom: auto;
        display: flex;
        justify-content: space-around;
    }
}

@include tablet-p {
    .QuestionnaireCardHome {
        width: 330px;
        height: 170px;
        padding: 0px 20px;
    }
}

@include desktop {
    .QuestionnaireCardHome {
        height: 26.34vh;
        width: 26.47vw;
        padding: 20px 0;

        &__Options {
            width: 7.6vw;
            margin: 2.5vh auto auto;
        }
    }
}