@import '../../../assets/mixins/mixins.scss';


.QuestionnaireFinish {
    margin: auto;
    width: 270px;
    display: flex;
    height: 130px;

    & .QuestionnaireTitle {
        margin: auto;
    }
}

@include tablet-p {
    .QuestionnaireFinish {
        display: flex;
        width: 330px;
        height: 170px;

        & .QuestionnaireTitle {
            font-size: 16px;
            margin: auto;
            height: fit-content;
        }
    }
}

@include desktop {
    .QuestionnaireFinish {
        width: 34.16vw;
        height:39.5vh;
        margin: auto;
        padding: 0 40px;

        & .QuestionnaireTitle {
            font-size: 3vh;
        }
    }
}
