@import '../../../assets/mixins/mixins.scss';

.QuestionnaireCardBill {
    display: flex;
    flex-direction: column;
    height: 140px;
    width: 280px;

    & .QuestionnaireTitle {
        margin: auto auto 0;
        font-size: 14px;
    }

    &__Counter {
        font-size: 16px;
        margin: 10px auto 10px;
    }

    &__Input[type=range] {
        margin: 0 auto 0;
        width: 80%;
        height: 0;
        -webkit-appearance: none;
        background-color: unset;
        
        &::-webkit-slider-runnable-track {
            background-color: #707070;
            height: 4px;
            width: 10px;
            border-radius: 2px;

            &:hover {
                cursor: pointer;
            }
        }

        &::-webkit-slider-thumb {
            box-shadow: 0px 0px 0px #000000;
            border: 0px solid #000000;
            height: 10px;
            width: 25px;
            border-radius: 7px;
            background: #464646;
            cursor: pointer;
            -webkit-appearance: none;
            margin-top: -3.5px;

            &:hover {
                cursor: pointer;
            }
        }

        &:focus {
            outline: none;
        }
    }

    & datalist {
        display: flex;
        height: 0px;
        justify-content: space-between;
        width: 80%;
        user-select: none;
        margin: 20px auto 10px;
    }

    & .CTA {
        padding: 0;
        width: 60px;
        height: 20px;
        margin: 0 auto auto;

        &__Text {
            font-size: 14px;
        }
    }
}

@include tablet-p {
    .QuestionnaireCardBill {
        margin-top: 0;
        width: 330px;
        height: 180px;
        padding: 0;

        & .QuestionnaireTitle {
            font-size: 20px;
            padding: 0 20px;
            margin: auto auto 0;
        }

        &__Counter {
            margin-top: 5px;
        }

        & .CTA {
            width: 60px;
            height: 20px;
            padding: 0;
            margin: 0 auto auto;

            &__Text {
                font-size: 16px;
            }
        }

        &__Input[type=range] {
            margin-top: unset;

            &::-webkit-slider-runnable-track {
                background-color: #707070;
                height: 6px;
                width: 10px;
                border-radius: 2px;
    
                &:hover {
                    cursor: pointer;
                }
            }
    
            &::-webkit-slider-thumb {
                box-shadow: 0px 0px 0px #000000;
                border: 0px solid #000000;
                height: 14px;
                width: 25px;
                border-radius: 7px;
                background: #464646;
                cursor: pointer;
                -webkit-appearance: none;
    
                &:hover {
                    cursor: pointer;
                }
            }
        }

        & datalist {
        }
    }
}

@include desktop {

    .QuestionnaireCardBill {
        width: 34.16vw;
        height:39.5vh;
        margin: auto;
        padding-top: 3vh;

        & .QuestionnaireTitle {
            font-size: 3vh;
        }

        &__Counter {
            margin-top: 2vh;
            font-size: 2.5vh;
        }

        &__Input[type=range] {
            margin-top: 1vh;
            margin-bottom: 0.5vh;

            &::-webkit-slider-runnable-track {
                background-color: #707070;
                height: 10px;
                width: 10px;
                border-radius: 2px;
    
                &:hover {
                    cursor: pointer;
                }
            }
    
            &::-webkit-slider-thumb {
                box-shadow: 0px 0px 0px #000000;
                border: 0px solid #000000;
                height: 15px;
                width: 25px;
                border-radius: 7px;
                background: #464646;
                cursor: pointer;
                -webkit-appearance: none;
                margin-top: -3.5px;
    
                &:hover {
                    cursor: pointer;
                }
            }
        }

        & datalist {
            margin-top: 20px;
        }

        & .CTA {
            padding: 10px 0;
            width: 120px;
            height: fit-content;
            margin: 10px auto auto;

            &__Text {
                font-size: 2.5vh;
            }
        }
    }
}