@import '../../assets/mixins/mixins.scss';

.NavBar {
    position: fixed;
    display: flex;
    width: 100%;
    z-index: 50;
    outline: none;
    user-select: none;
    
    &__Logo {
        width: 67.5px;
        position: absolute;
        left: 10px;
    }

    &__Container {
        display: flex;
        margin: 0 auto;

        &__Link {
            // color: #51B03E;
            color: #000;
            font-family: Arial, Helvetica, sans-serif;
            font-weight: bold;
            font-size: 12px;
            padding-top: 12px;
            border-top: 4px solid #51b03e00;
            cursor: pointer;
            height: fit-content;
            text-decoration: none;

            &:not(:first-of-type) {
                margin-left: 20px;
            }
        }
    }
    
}

.Active-Nav {
    border-top: 4px #000 solid;
}

@include tablet-p {

    .NavBar {
        position: fixed;
        display: flex;
        width: 100%;
        z-index: 50;
        background-color: rgb(219, 219, 219);
        height: 90px;
        
        &__Logo {
            width: 100px;
            height: auto;
            left: 20px;
            top: 10px;
        }
        
    }

    .Active-Nav {
        border-top: 4px #000 solid;
    }

}