@import '../../../assets/mixins/mixins.scss';

.Booker {
    position: fixed;
    height: 100vh;
    width: 100vw;
    display: flex;
    z-index: 999;
    backdrop-filter: blur(15px) brightness(105%);

    &:hover {
        cursor: pointer;  
    }

    &__X {
        position: absolute;
        top: 10px;
        right: 20px;
        font-family: sans-serif;
        color: white;
        font-size: 24px;
    }

    &__Form {
        cursor: default;
        margin: auto;
        height: 80vh;
        width: 80vw;
        background-color: #242B3A;
        display: flex;
        box-shadow: 0 0 20px rgba(255, 255, 255, 0.644);

        &__Container {
            margin: auto;
            width: 70vw;
            padding: 0 20px;
            display: flex;
            flex-direction: column;

            &__Label {
                font-family: sans-serif;
                font-size: 20px;
                color: White;
            }

            & .InputBox {
                width: 90%;
                height: 30px;
                padding: 5px;

                &:first-of-type {
                }

                &:not(:last-of-type) {
                    margin-bottom: 20px;
                }
            }

            &__DatePicker {
                margin-top: 10px;
                margin-bottom: 20px;
            }

            & .CTA {
                border: solid black 1px;
                margin-bottom: auto;
            }
        }

        
    }
}

@include tablet-p {
    .Booker {

        &__X {
            top: 20px;
            right: 40px;
            font-size: 34px;
        }

        &__Form {

            &__Container {
                margin: auto;
                width: fit-content;
                &__Label {

                }
    
                & .InputBox {
                    width: 100%;
                    margin: 0 auto 0 0;
                }
    
                &__DatePicker {
                    width: 60%;
                }
            }
        }
    }
}

@include desktop {
    .Booker {
        &__Form {
            width: 50vw;
        }
    }
}