@import '../../../assets/mixins/mixins.scss';

.QuestionnaireCardZipCode {
    width: 250px;
    height: 140px;
    display: flex;
    flex-direction: column;
    margin: unset;

    & .QuestionnaireTitle {
        margin: auto auto 0;
    }

    & .InputBox {
        font-size: 14px;
        text-align: center;
        margin: 15px auto 0;
        width: 100px;
        padding: unset;

        &::placeholder {
            font-size: 14px;
        }
    }

    & .CTA {
        height: 20px;
        padding: 0;
        width: 50px;
        margin: 10px auto auto;

        &__Text {
            font-size: 14px;
        }
    }
}

@include tablet-p {
    .QuestionnaireCardZipCode {

        & .QuestionnaireTitle {
            font-size: 20px;
            margin-top: auto;
        }

        & .InputBox {
            margin: 10px auto 0;
            width: 100px;
            height: 30px;
            padding: 5px 1px;
        }

        & .CTA {
            margin: 10px auto auto;
            padding: unset;
            width: 60px;

            &__Text {
                font-size: 15px;
            }
        }
    }
}

@include desktop {
    .QuestionnaireCardZipCode {
        height: 26.34vh;
        width: 26.47vw;
        display: flex;
        flex-direction: column;

        & .QuestionnaireTitle {
            font-size: 3vh;
        }

        & .InputBox {
            margin: 1.5vh auto ;
            width: 160px;
        }

        & .CTA {
            margin: 1vh auto auto;
            width: 100px;
            height: 30px;
            
            &__Text {
                font-size: 2.5vh;
            }
        }
    }
}