@import '../../assets/mixins/mixins.scss';

.NavAccordion {
    margin: 50px auto auto;
    width: 100%;
    height: calc(100vh - 85px);
    overflow-y: scroll;
    padding: 5px 10px;
    

    &__Item {
        margin: 10px auto;
        font-family: 'Monteserrat', sans-serif ;
        font-weight: 400;
        font-size: 20px;

        &__Question-Heading {

            &__Button {
                border-radius: 5px;
                height: 60px;
                display: flex;
                background-color: #fff;
                padding-left: 15px;
                padding-right: 15px;
                align-items: center;

                &:hover {
                    cursor: pointer;
                }

                &:focus  {
                    outline: none;
                }
            }
        }

        &__Question-Pannel {
            font-size: 16px;
            font-weight: normal;
            padding: 14px 20px 5px;
            white-space: pre-wrap;
        }
    }
}

@include tablet-p {
    .NavAccordion {
        margin-top: 90px;
        width: 90%;
        margin: none;
        height: calc(100vh - 150px);
        overflow-y: unset;
    
    }
}

